import { Component, OnInit } from '@angular/core';
import { DataService } from '../data.service';
import { LoopBackConfig, BASE_URL, API_VERSION } from '../shared/sdk';
import { Member, AccessToken } from '../shared/sdk/models';
import { MemberApi } from '../shared/sdk/services';
import { Router } from '@angular/router';
import { SharedService } from '../shared.service';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss']
})
export class DashboardComponent implements OnInit {
  public userData: any = [{isLogin: false, userData: {'firstName': 'piyush', 'lastName': 'kriplani'}}];
  constructor(private memberApi: MemberApi,
              private router: Router,
              private dataService: DataService,
              private sharedService: SharedService
  ) {
    LoopBackConfig.setBaseURL(BASE_URL);
    LoopBackConfig.setApiVersion(API_VERSION);
  }
  ngOnInit() {
    if (this.memberApi.isAuthenticated()) {
      this.userData['isLogin'] = true;
      this.sharedService.setSession(this.userData);
    } else {
      this.sharedService.setSession([]);
    }
  }

  logout() {
    this.memberApi.logout().subscribe((data) => {
      this.router.navigate(['/login']);
    }, (err) => {
      console.log(err);
    });
  }

}



