import { Component, OnInit } from '@angular/core';
import { Member, AccessToken} from '../../shared/sdk/models';
import { MemberApi } from '../../shared/sdk/services';
import { Router } from '@angular/router';
import { DataService } from '../../data.service';
import { SharedService} from '../../shared.service';

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.scss']
})
export class NavbarComponent implements OnInit {
  isLogin = false;
  isToggleUp = false;
  userDate: any = [];
  constructor(private memberApi: MemberApi,
              private router: Router,
              private dataService: DataService,
              private sharedService: SharedService) { }
  ngOnInit() {
    this.isLogin = this.memberApi.isAuthenticated();
    this.sharedService.session$.subscribe((data) => {
      if (data && data.length) {
        this.userDate = data;
        }
    });
  }

  logout() {
    this.isLogin = false;
    this.memberApi.logout().subscribe((data) => this.setAuthChanged(false));
  }

  setAuthChanged(flag) {
    this.userDate = [];
    this.dataService.setAutenticationChanged(flag);
    this.router.navigate(['/login']);
  }

}



