import { Injectable } from '@angular/core';
import { Subject } from 'rxjs/Subject';

@Injectable()
export class SharedService {

  private session = new Subject<string[]>();
  session$ = this.session.asObservable();

  constructor() { }

  setSession(userData) {
    this.session.next(userData);
  }

}



