import { Injectable, Output, EventEmitter } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/observable/throw';
import { Observer } from 'rxjs/Observer';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import { MemberApi } from './shared/sdk/services';
import { ConfigComponent } from './core/config';

@Injectable()
export class DataService {
  currentUser: any = null;
  @Output() authChanged: EventEmitter<boolean> = new EventEmitter<boolean>();
  constructor(private http: HttpClient, private memberApi: MemberApi, private config: ConfigComponent) {}
  getUserList(): Observable<any> {
    return this.http.get('http://192.168.1.107:3022/api/Members' +
      '?access_token=eJ37gOsxC3if8BPRvteHrgrnVKkNRcMd8hZoKb2VikrAYkka4UiBsLxo9M6bfz4y');
  }

  setAutenticationChanged(flag) {
    this.userAuthChanged(flag);
  }

  private userAuthChanged(status: boolean) {
    this.authChanged.emit(status);
  }

  handleError(error: any) {
    console.error('server error:', error);
    if (error instanceof Response) {
      let errMessage: any = '';
      try {
        errMessage = error.json();
      } catch (err) {
        errMessage = error.statusText;
      }
      return Observable.throw(errMessage);
    }
    return Observable.throw(error || 'Node.js server error');
  }

}



